@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.3s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.3s ease-in-out;
  }
  .underline-w:hover {
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.dev-info {
  overflow: hidden;
  position: relative;
  background: #000;
}
.dev-info__header {
  width: 100%;
}
.dev-info__images {
  pointer-events: none;
  position: absolute;
  z-index: 3;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background: radial-gradient(circle at 100% 75%, rgba(110, 123, 177, 0.568627451) 0%, rgba(43, 55, 105, 0.3647058824) 15%, transparent 75%);
}
.dev-info__img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.dev-info__title {
  max-width: 920px;
  color: #fff;
  position: relative;
  z-index: 2;
}
@media screen and (max-width: 1440px) {
  .dev-info__title {
    margin-bottom: 25px;
  }
}
@media screen and (max-width: 690px) {
  .dev-info__title {
    text-align: center;
  }
}
.dev-info__content {
  position: relative;
  z-index: 2;
  padding: 13rem 50px 16.2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 40px;
}
@media screen and (max-width: 1440px) {
  .dev-info__content {
    padding: 13rem 30px 16.2rem;
  }
}
@media screen and (max-width: 1440px) {
  .dev-info__content {
    padding: 60px 30px;
  }
}
@media screen and (max-width: 375px) {
  .dev-info__content {
    padding: 60px 10px;
  }
}
.dev-info__column {
  overflow: visible;
  position: relative;
  z-index: 2;
}
.dev-info__column:nth-child(1) {
  grid-column: 1/3;
}
.dev-info__column:nth-child(2) {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 0;
}
.dev-info__column:nth-child(3) {
  padding-top: 65px;
}
.dev-info__column:nth-child(4) {
  grid-column: 1/3;
}
@media screen and (max-width: 960px) {
  .dev-info__column:nth-child(2) {
    grid-column: 1/3;
    grid-row: 2/3;
    position: relative;
    z-index: 1;
    opacity: 0.1;
  }
  .dev-info__column:nth-child(3) {
    grid-column: 1/3;
    grid-row: 2/3;
    position: relative;
    z-index: 2;
  }
}
.dev-info__image {
  pointer-events: none;
  overflow: visible;
  z-index: 1;
  width: 100%;
  height: 100%;
  max-width: 900.52px;
  background: url("../img/performance/macbook.png") center/contain no-repeat;
}
.dev-info__image-bg {
  overflow: visible;
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../img/performance/ellipse-01.png") right/105% no-repeat;
  transform: translate(20px, 20px);
  opacity: 0.3;
}
.dev-info__footer {
  display: flex;
  gap: 40px;
}
@media screen and (max-width: 960px) {
  .dev-info__footer {
    gap: 20px;
  }
}
@media screen and (max-width: 690px) {
  .dev-info__footer {
    flex-direction: column;
    gap: 5px;
  }
}

.info-dev__row {
  border-top: 1px solid rgba(169, 169, 169, 0.411);
  padding: 20px 0 83px;
}
.info-dev__row p {
  line-height: 122.857143%; /* 43px/35 */
  color: #fff;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .info-dev__row {
    padding-bottom: calc(20px + 63 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .info-dev__row {
    padding: 10px 0 20px;
  }
}

.footer-dev {
  border-top: 1px solid rgba(169, 169, 169, 0.4117647059);
}
.footer-dev__column {
  padding-top: 29px;
  flex: 0 1 50%;
}
@media screen and (max-width: 690px) {
  .footer-dev__column {
    padding-top: 13px;
  }
  .footer-dev__column:not(:first-child) {
    padding-top: 0;
  }
}
.footer-dev__column p {
  color: #fff;
  line-height: 125%; /* 20px/16 */
}